/* footer styling */ 
.footer-menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; /* Ensures wrapping on smaller screens */
  gap: 15px; /* Spacing between menu items */
}

.footer {
  background: $colour7;
  color: $colour1;
  text-align: center;
  min-height: 56px;
  line-height: 45px;
  width: $container-width;
  padding: 20px;
}
/* links inside the footer*/ 
.footer a {
  font-size: 15px;
  font-weight: 600;
  line-height: 56px;
  padding: 5px 10px;
  text-decoration: none;
}

/* responsive adjustments for smaller screens */ 
@media (max-width: $container-width) {
  .footer {
    background: $colour7; 
    color: $colour1;
    text-align: center;
    min-height: 56px;
    line-height: 45px;
    width: 95vw;
  }
}

/* footer description */ 
.footer-description {
  display: block;
  margin-bottom: 1rem;
  font-size:0.8rem;
}

.footer-description a { 
  text-decoration: none;
}

/* Collab. logos */ 
.footer-logos {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px; 
  margin-top: 10px;

  .footer-logo {
    max-width: 300px; 
    height: 75px;
    opacity: $logo-opacity; 

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
}


